<template>
  <div class="info-wrapper">
    <div class="order-info">
      <div class="top">
        <div class="name-price ">
          <p class="name">{{orderInfo.name}}</p>
          <p class="price">{{orderInfo.price}}<span>元</span></p>
        </div>
        <p class="order-sn">订单编号：{{orderInfo.sn}}</p>
      </div>
      <div class="order-info-wrapper ">
        <div class="sec ">
          <div class="title-name" @click="ttrShow=!ttrShow">
            <span class="title">投保人信息</span>
            <p>
              <span class="name">{{tbrInfo.name}} <i :style="ttrShow ? upImg : downImg"></i></span>
            </p>
          </div>
          <div class="info-con" v-show="ttrShow">
            <p class="item"><i>姓名</i><span>{{tbrInfo.name}}</span></p>
            <p class="item"><i>性别</i><span>{{getSexByName(tbrInfo.gender)}}</span></p>
            <p class="item"><i>身份证号</i><span>{{tbrInfo.id_num}}</span></p>
            <p class="item"><i>出生日期</i><span>{{dateFormat(tbrInfo.birth)}}</span></p>
            <p class="item"><i>联系电话</i><span>{{tbrInfo.mobile}}</span></p>
          </div>
        </div>
        <div class="sec">
          <div class="title-name" @click="bttrShow=!bttrShow">
            <span class="title">被保险人信息</span>
            <p>
              <span class="name">{{bbxrInfo.name}}  <i :style="bttrShow ? upImg : downImg"></i></span>
            </p>
          </div>
          <div class="info-con" v-show="bttrShow">
            <p class="item"><i>姓名</i><span>{{bbxrInfo.name}}</span></p>
            <p class="item"><i>性别</i><span>{{getSexByName(bbxrInfo.gender)}}</span></p>
            <p class="item"><i>身份证号</i><span>{{bbxrInfo.id_num}}</span></p>
            <p class="item"><i>出生日期</i><span>{{dateFormat(bbxrInfo.birth)}}</span></p>
            <p class="item"><i>是投保人的</i><span>{{getRelation(bbxrInfo.type)}}</span></p>
          </div>
        </div>
        <div class="sec ">
          <div class="title-name">
            <span class="title">受益人</span>
            <p>
              <span class="name syr-name">{{orderInfo.beneficiary}}</span>
            </p>
          </div>
        </div>
        <div class="sec sec-bzfa">
          <div class="title-name " @click="bzfaShow=!bzfaShow">
            <span class="title">保障方案</span>
            <p class="name">
              <span v-if="bzfaInfo.plan_name">{{bzfaInfo.plan_name}}</span>
              <i :style="bzfaShow ? upImg : downImg"></i>
            </p>
          </div>
          <div class="bzfa-wrapper" v-show="bzfaShow">
            <div class="plan-info-item" v-for="(item,index) in bzfaInfo.plan_details" :key="index">
               <span class="bzxm">{{item.name}}
               </span>
              <span>{{item.value}}</span>
            </div>
          </div>
          <div class="info-con" v-show="bzfaShow">
            <p class="item"><i>保险期间</i><span>{{orderInfo.insurance_period}}</span></p>
            <p class="item "><i>起始日期</i><span>{{orderInfo.policy_effective_date}}</span></p>
            <p class="item "><i>终止日期</i><span>{{orderInfo.policy_expiration_date}}</span></p>
          </div>
        </div>
      </div>
      <ul class="pay-way">
        <li v-if="isWechtShow">
          <input type="radio" id="wechat" value="wxpayWeb" v-model="payWay">
          <label for="wechat"><img src="../../assets/images/wechat.png" alt="zfb">微信</label>
        </li>
        <li>
          <input type="radio" id="zfb" value="alipayWeb" v-model="payWay">
          <label for="zfb"><img src="../../assets/images/zfb.png" alt="zfb">支付宝</label>
        </li>

      </ul>
    </div>
    <div class="bottom">
      <div class="tips ">
        <p class="read">
          <input type="checkbox" value="3" v-model="read" id="read">
          <label for="read"></label>
        </p>
        <p class="protocal">我已阅读本产品 <span>《保险条款》</span><span
        >《客户告知书》</span>等信息，并充分理解和接受。</p>
      </div>
      <div class="btns">
        <span class="back" @click="amendOrder">返回修改订单</span>
        <span class="pay" @click="onPay">立即支付</span>
      </div>
    </div>

  </div>
</template>

<script>
import { getSexByName, dateFormat, getRelation } from '@/assets/js/mixins/base.js'

export default {
  name: 'TheOrderComfirmInfo',
  mixins: [getSexByName, dateFormat, getRelation],
  data () {
    return {
      isWechtShow: false,
      orderInfo: {},
      tbrInfo: {},
      bbxrInfo: {},
      bzfaInfo: {},
      ttrShow: false,
      bttrShow: false,
      bzfaShow: true,
      payWay: 'wxpayWeb',
      read: false,
      upImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_up.png') + ')'
      },
      downImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_down.png') + ')'
      }
    }
  },
  mounted () {
    this.getOrderInfo()
    this.isWecht()
  },
  methods: {
    isWecht () {
      const ua = navigator.userAgent.toLowerCase()
      if (ua.match(/MicroMessenger/i) === 'micromessenger') {
        this.isWechtShow = true
      } else {
        this.isWechtShow = false
        this.payWay='alipayWeb'
      }
    },
    goZfbPay () {
      let postData = {
        orderSn: this.orderInfo.sn,
        type: this.payWay
      }
      this.$api.product.orderPay(postData).then(res => {
        if (res.code === 1) {
          location.href = `${process.env.VUE_APP_BASE_URL}${res.data}`
          // window.open(`${process.env.VUE_APP_BASE_URL}${res.data}`)
          this.ispayShow = true
        } else {
          this.$toast.center(res.message)
        }
      })
    },
    goWechatPay () {
      this.$router.push({
        path: '/product/wechatPay',
        query: {
          price: this.orderInfo.price,
          orderSn: this.orderInfo.sn
        }
      })
    },
    onPay () {
      if (this.read) {
        let postData = {
          orderSn: this.orderInfo.sn
        }
        this.$api.product.orderCheck(postData).then(res => {
          if (res.code === 1) {
            if (this.payWay === 'wxpayWeb') {
              this.goWechatPay()
            } else {
              this.goZfbPay()
            }
          } else {
            this.$toast.center(res.message)
          }
        })
      } else {
        this.$toast.center('请先阅读并确认页面下方的条款')
      }
    },
    getOrderInfo () {
      let postData = {
        sn: this.$route.query.sn
      }

      this.$api.product.getOrderInfo(postData).then(res => {
        if (res.code === 1) {
          this.orderInfo = res.data
          this.tbrInfo = this.orderInfo.member_info
          this.bbxrInfo = this.orderInfo.relation_info
          this.bzfaInfo = this.orderInfo.plan_info
        }
      })
    },
    amendOrder () {
      this.$router.push({
        path: `/product/detail/${this.orderInfo.product_id}`,
        query: {
          productId: this.orderInfo.product_id,
          orderSn: this.orderInfo.sn
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .order-info {
    /*background: #fff;*/
    /*box-shadow: 0 2px 4px 0 rgba(219, 219, 219, 0.35);*/
    /*padding: 20px 20px 8px 20px;*/
    padding-bottom: pxToRem(110px);
    .top {
      padding: pxToRem(29px) pxToRem(30px) pxToRem(20px);
      .name-price {
        display: flex;
        bottom: pxToRem(200px);
        justify-content: space-between;
        margin-bottom: pxToRem(20px);
        .name {
          font-size: pxToRem(36px);
          color: $color-text;
          font-weight: bold;
        }

        .price {
          font-size: pxToRem(54px);
          color: $color-active-text;
          span {
            font-size: pxToRem(24px);
            color: #D5D8D8;
          }
        }
      }
      .order-sn {
        font-size: pxToRem(24px);
        color: #A5A8A8;
      }
    }

    .order-info-wrapper {
      background: #fff;
      .sec {
        padding-left: pxToRem(30px);
        .title-name {
          display: flex;
          justify-content: space-between;
          border-bottom: $border-bottom-light-color;
          padding-right: pxToRem(20px);
          height: pxToRem(90px);
          line-height: pxToRem(90px);
          .title {
            font-size: pxToRem(28px);
            color: $color-text;
          }
          .name {
            font-size: pxToRem(28px);
            color: $color-text;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            &.syr-name {
              padding-right: pxToRem(10px);
            }
            i {
              display: inline-block;
              width: pxToRem(44px);
              height: pxToRem(44px);
              background-repeat: no-repeat;
              background-position: right center;
              background-size: cover;
              padding-left: pxToRem(10px);
            }
          }
        }
        .info-con {
          position: relative;
          border-bottom: $border-bottom-light-color;
          padding: pxToRem(16px) pxToRem(32px) pxToRem(17px) 0;
          .item {
            font-size: pxToRem(24px);
            color: #A5A8A8;
            line-height: pxToRem(42px);
            display: flex;
            justify-content: space-between;
            width: 100%;
            i {
              display: inline-block;
            }
          }
          .item-syr {
            font-size: 14px;
            i {
              width: 56px;
            }
          }
        }
        .bzfa-wrapper {
          padding: pxToRem(16px) pxToRem(30px) 0 0;
          .plan-info-item {
            display: flex;
            justify-content: space-between;
            color: #A5A8A8;
            line-height: pxToRem(42px);
            font-size: pxToRem(24px);
          }
        }
      }
    }
    ul.pay-way {
      margin-top: pxToRem(20px);
      background: #fff;
      li {
        position: relative;
        padding: pxToRem(27px) pxToRem(28px) pxToRem(27px) pxToRem(32px);
        border-bottom: 1px solid #F1F4F4;
        box-sizing: border-box;
        font-size: pxToRem(28px);
        color: $color-text;
        label {
          img {
            vertical-align: middle;
            width: pxToRem(36px);
            line-height: pxToRem(36px);
            margin-right: pxToRem(20px);
          }
        }
        input[type="radio"] {
          position: absolute;
          clip: rect(0, 0, 0, 0);
        }
        input[type="radio"] + label:before {
          /*position: absolute;*/
          content: "";
          width: pxToRem(34px);
          height: pxToRem(34px);
          background: #FFFFFF;
          border: 1px solid #D5D8D8;
          border-radius: 100%;
          display: inline-block;
          float: right;
          position: absolute;
          top: pxToRem(29px);
          right: pxToRem(28px);
          box-sizing: border-box;
        }
        input[type="radio"]:checked + label::before {
          background-color: $color-active-text;
          background-clip: content-box;
          width: pxToRem(34px);
          height: pxToRem(34px);
          padding: pxToRem(9px);
        }
      }
    }

  }

  .bottom {
    background: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    box-shadow: 0 -4px 16px 0 rgba(225, 225, 225, 0.64);
    z-index: 200;
    .tips {
      position: relative;
      font-size: pxToRem(24px);
      color: #A5A8A8;
      line-height: pxToRem(38px);
      padding: pxToRem(12px) pxToRem(28px) pxToRem(12px) pxToRem(32px);
      border-bottom: $border-bottom-light-color;
      .read {
        input[type="checkbox"] {
          position: absolute;
          clip: rect(0, 0, 0, 0);
        }
        input[type="checkbox"] + label:before {
          position: absolute;
          content: "";
          width: pxToRem(32px);
          height: pxToRem(32px);
          background: #FFFFFF;
          border: 1px solid #D5D8D8;
          display: inline-block;
          top: pxToRem(17px);;
          left: pxToRem(32px);
          box-sizing: border-box;
        }
        input[type="checkbox"]:checked + label::before {
          content: "";
          color: #01cd78;
          background: url("../../assets/images/icon_read.png") no-repeat center center;
          background-size: cover;
        }
      }
      .protocal {
        padding-left: pxToRem(48px);
        span {
          cursor: pointer;
          color: #3875E2;
        }
      }
    }
    .btns {
      display: flex;
      justify-content: space-between;
      text-align: center;
      height: pxToRem(100px);
      line-height: pxToRem(100px);
      span {
        display: block;
        width: 50%;
        font-size: pxToRem(34px);
      }
      .back {
        color: #1EDBD5;
      }
      .pay {
        color: #FFFFFF;
        background-image: linear-gradient(-135deg, #21DED8 0%, #12D2CC 100%);
      }
    }
  }
</style>
